import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: "/",
      redirect: "/textflint",
      component: () => import("@/view/layout/Layout"),
      children: [
        {
          path: "/textflint",
          name: "textflint",
          component: () => import("@/view/pages/Textflint.vue"),
        },
        {
          path: "/chartdisplay",
          name: "chartdisplay",
          component: () => import("@/view/pages/ChartDisplay.vue"),
        },
        {
          path: "/builder",
          name: "builder",
          props: true,
          component: () => import("@/view/pages/Builder.vue"),
        },
      ],
    },
    {
      path: "/tutorials",
      redirect: "/tutorials",
      component: () => import("@/view/layout/Layout.vue"),
      children: [
        {
          path: "/tutorials",
          name: "tutorials",
          component: () => import("@/view/pages/Demo.vue"),
        },
      ],
    },
    {
      path: "/demo",
      redirect: "/tutorials",
      component: () => import("@/view/layout/Layout.vue"),
      children: [
        {
          path: "/tutorials",
          name: "tutorials",
          component: () => import("@/view/pages/Demo.vue"),
        },
        {
          path: "/demo",
          name: "demo",
          component: () => import("@/view/pages/Demo.vue"),
        },
      ],
    },
    {
      path: "/human_evaluation",
      redirect: "/human_evaluation",
      component: () => import("@/view/layout/Layout.vue"),
      children: [
        {
          path: "/human_evaluation",
          name: "human_evaluation",
          component: () => import("@/view/pages/HumanEvaluation.vue"),
        },
      ],
    },
    {
      path: "*",
      redirect: "/404",
    },
    {
      // the 404 route, when none of the above matches
      path: "/404",
      name: "404",
      component: () => import("@/view/pages/error/Error-1.vue"),
    },
  ],
});
