export const SET_TASKINFO = "setTaskInfo";
export const SET_TASKNAME = "setTaskName";

export default {
  state: {
    name: "",
    full_name: "",
    description: "",
    available_domain: [],
    available_ut: []
  },
  getters: {
    /**
     * Get list of breadcrumbs for current page
     * @param state
     * @returns {*}
     */
    name(state) {
      return state.name;
    }
  },
  actions: {
    /**
     * Set the breadcrumbs list
     * @param state
     * @param payload
     */
    [SET_TASKINFO](state, payload) {
      state.commit(SET_TASKINFO, payload);
    },
    [SET_TASKNAME](state, payload) {
      state.commit(SET_TASKNAME, payload);
    }
  },
  mutations: {
    [SET_TASKINFO](state, payload) {
      state.name = payload.name;
      state.full_name = payload.full_name;
      state.description = payload.description;
      state.available_domain = payload.available_domain;
      state.available_ut = payload.available_ut;
    },
    [SET_TASKNAME](state, payload) {
      state.name = payload;
    }
  }
};
